import React from 'react';
// import logo from './logo.svg';
import './RecipeApp.css';
// import Recipe from './Recipe';
import Navbar from './navbar';
import RecipeInput from './RecipeInput';
import Recipelist from "./Recipelist";
// import { render } from 'react-dom';

class RecipeApp extends React.Component {
  constructor(props) {
    super();
    this.state = {
      recipes: [
        {
            id: 1,
            title:"Mutton Nihari",
            instructions:"Get 1kg of mutton shanks and wash them. Pour oil in the pressure cooker and heat it up. Put the meat in the cooker and fry it thoroughly til it changes into light brown color. The add some water and nihari masal along with 3 spoons to ghee. Let it cook for next 30 mins till the oil gets seperated from the gravy. The scoop out the oil and pour it in another container and add in a mix of gram flour and water in the cooker and cook for another 10 mins. Now you mutton nihari is ready to be served.",
            ingredients: ["1 kg Mutton shanks", "4-5 kg of water", "spices", "gram flour"],
            img:"data:image/jpeg;base64,"
        },
        {
            id: 2,
            title:"Chicken Qorma",
            instructions:"Heat the vegetable oil into the pan, then put a dollop of ghee.Put cardamom, cloves, garlic and fry the mix properly.Then add the chicken and let it cook for about 2-3 minutes.Keep stirring it.Once it becomes brown, add coriander and chili powder.Add salt to taste.Add ginger paste, mixture of fried onions and yogurt, let it cook for a minute.Then put garam masala and saffron for flavor.If the gravy is too thick add little bit of water before covering it. The masala should fuse well with the chicken. Cover it and give it some time to cook on slow fire. Let it simmer for about 10-15 minutes,Keep stirring occasionally.Serve hot, garnished with coriander leaves.",
            ingredients: ["chicken", "oil", "ghee", "Cardamoms", "Cloves"],
            img:"data:image/jpeg;base64,"
        }
    ],
    nextRecipeId:2,
    showForm: false
    }
    this.handleSave = this.handleSave.bind(this);
    this.ondelete = this.ondelete.bind(this);
  }

  handleSave(recipe){
    this.setState((prevState, props) => {
      const newRecipe = {...recipe, id:this.state.nextRecipeId}
      return{
        nextRecipeId: prevState.nextRecipeId + 1,
        recipes:[...this.state.recipes, newRecipe],
        showForm:false
      }
    });

  }

  ondelete(id){
    const recipes = this.state.recipes.filter(r => r.id!== id);
    this.setState({recipes});
  }

  render() {
    const {showForm} = this.state;
    return (
 
      <div className="App">
        <Navbar onNewRecipe={()=>this.setState({showForm: true})}/>
       {showForm ? <RecipeInput onSave ={this.handleSave} onclose={() => this.setState({showForm:false})}/> : null}
        <div className="recipelist">
          <Recipelist ondelete={this.ondelete} recipes={this.state.recipes} />
        </div>
        
      </div>
    );
  }
}

export default RecipeApp;
